<template>
  <div class="content">
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="账号" prop="name">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input v-model="ruleForm.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
      </el-form-item>
      <el-form-item>
        账号：aaa   bbb   ccc
        密码：随便
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        name: "",
        password: ""
      },
      rules: {
        name: [{ required: true, message: "请输入账号", trigger: "blur" }],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }]
      }
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if(this.ruleForm.name!='aaa'&&this.ruleForm.name!='bbb'&&this.ruleForm.name!='ccc'){
            this.$message({
              message: "请输入正确的账号",
              type: "error"
            });
            return false
          }
          this.$message({
            message: "登录成功",
            type: "success"
          });
          //个人信息
          this.$store.commit('SET_USER',this.ruleForm)
          // 权限标识
          /* 
            路由权限思路，大概分为两类
            一、根据自己，添加账户的时候用下拉框选择的权限，然登录返回这个登录标识，根据标识遍历路由表，固定访问不同的页面
                例如：共有a,b,c 三种权限，
                a权限，能访问123页面，
                b权限，能访问2345页面，
                c权限，能访问5678页面，
                路由里添加meat标签，根据你当前权限遍历路由，有权限就加，没权限就删除
                meat: {
                  代表这个页面只有a,b权限可以访问
                  role: ['a', 'b']
                }
            二、根据自己，添加账户的时候用树形控件，勾选路由菜单，然后登录的时候直接返回路由表，直接add就行了
          二者使用场景：
              第一种：适合权限等级少的系统，不同的用户权限相对固定的，三五个角色那种，
                     使用者，使用简单，适合公司自己内部系统使用，一但开发完成，权限等级就
                     固定了，不易修改
              第二种：适合n个权限，来回修改权限的使用，每个人可以操作不同的页面，适合对外的系统，要不同的人都可以用，
                      相对灵活，等级权限自由设置，推荐使用，

            原理一样，在这里实现第一种，这个看懂，第二种就就很简单了      
          */
         //登录接口返回
          let role = this.ruleForm.name.substring(0,1)
          // token
          this.$store.commit('SET_TOKEN',this.ruleForm.name)
          this.$store.commit('SET_MENU', role)
          this.$router.push("/");
        } else {
          this.$message({
            message: "请填写信息",
            type: "warning"
          });
          return false;
        }
      });
    }
  }
  //created(){}
};
</script>
<style lang='less' scoped>
.content {
  width: 500px;
  height: 300px;
  margin: 200px auto 0;
}
</style>


